import { useState } from "react";
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

const Calendar = () => {

    const [startDate, setStartDate] = useState(new Date());
    const [endDate, setEndDate] = useState(null);
    const onChange = (dates) => {
        const [start, end] = dates;
        setStartDate(start);
        setEndDate(end);
    };
    return (
        <div style={{ overflow: "hidden" }}>
            <div style={{ marginBottom: "60px", float: "left", width: "50%" }}>
                <h3 style={{marginBottom: "20px"}}>  </h3>
                <div style={{marginBottom: "10px"}}> :</div>
                {startDate.getFullYear()+" "+(startDate.getMonth()+1)+" "+startDate.getDate()+""} <br/>
                {endDate===null ? '' : endDate.getFullYear()+" "+(endDate.getMonth()+1)+" "+endDate.getDate()+" "}
            </div>
            <div style={{ padding: "10px", float: "right" }}>
                <DatePicker
                    selected={startDate}
                    onChange={onChange}
                    startDate={startDate}
                    endDate={endDate}
                    selectsRange
                    inline />
            </div>
        </div>
    );
};

export default Calendar;

